"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Box, VStack, Text, Icon, Tabs } from "@chakra-ui/react";
import { LuSquareCheck } from "react-icons/lu";
import { FaShoppingCart } from "react-icons/fa";
import InputPesquisa from "@/components/InputPesquisa";
import { IoShirt } from "react-icons/io5";

const tabInicial = () => {
  const router = useRouter();
  const [searchTerm, setSearchTerm] = useState("");
  const [sidebarSection, setSidebarSection] = useState("loja"); // controla o que mostrar

  useEffect(() => {
    const token = localStorage.getItem("authToken");
    if (!token) {
      router.replace("/Login");
    }
  }, [router]);

  return (
    <Box minH="100vh" display="flex" style={{ background: "rgba(30, 43, 104, 0.97)" }}>
      {/* Sidebar permanente */}
      <Box
        w="220px"
        color="white"
        p={6}
        display="flex"
        flexDirection="column"
        alignItems="center"
        boxShadow="md"
        bgGradient="linear(to-t, blue.700, black)"
      >
        <Text fontWeight="bold" fontSize="xl" mb={8}>
          Menu
        </Text>
        <VStack spacing={6} align="stretch" w="100%">
          <Box
            as="button"
            w="100%"
            textAlign="left"
            py={2}
            px={4}
            borderRadius="md"
            bg={sidebarSection === "loja" ? "blue.600" : "transparent"}
            _hover={{ bg: "blue.600" }}
            onClick={() => setSidebarSection("loja")}
          >
            <Icon as={IoShirt} mr={2} />
            Loja
          </Box>
          <Box
            as="button"
            w="100%"
            textAlign="left"
            py={2}
            px={4}
            borderRadius="md"
            bg={sidebarSection === "carrinho" ? "blue.600" : "transparent"}
            _hover={{ bg: "blue.600" }}
            onClick={() => setSidebarSection("carrinho")}
          >
            <Icon as={FaShoppingCart} mr={2} />
            Carrinho
          </Box>
          <Box
            as="button"
            w="100%"
            textAlign="left"
            py={2}
            px={4}
            borderRadius="md"
            bg={sidebarSection === "config" ? "blue.600" : "transparent"}
            _hover={{ bg: "blue.600" }}
            onClick={() => setSidebarSection("config")}
          >
            <Icon as={LuSquareCheck} mr={2} />
            Configurações
          </Box>
        </VStack>
      </Box>

      {/* Conteúdo principal */}
      <Box flex="1" p={0} m={0} display="flex" flexDirection="column" alignItems="center">
        {sidebarSection === "loja" && (
          <>
            <Tabs.Root defaultValue="loja" orientation="horizontal" w="100%" maxW="900px" mt={6}>
              <Tabs.List
                bg="white"
                borderRadius="2xl"
                boxShadow="lg"
                px={4}
                py={3}
                gap={6}
                justifyContent="center"
                display="flex"
                border="2px solid"
                borderColor="blue.200"
              >
                <Tabs.Trigger
                  value="loja"
                  fontSize="xl"
                  fontWeight="bold"
                  px={12}
                  py={5}
                  borderRadius="lg"
                  transition="all 0.2s"
                  _selected={{ bg: "blue.500", color: "white", boxShadow: "md", transform: "scale(1.08)" }}
                  _hover={{ bg: "blue.100", color: "blue.700" }}
                  display="flex"
                  alignItems="center"
                  gap={3}
                >
                  <FaShoppingCart />
                  Futebol
                </Tabs.Trigger>
                <Tabs.Trigger
                  value="carrinho"
                  fontSize="xl"
                  fontWeight="bold"
                  px={12}
                  py={5}
                  borderRadius="lg"
                  transition="all 0.2s"
                  _selected={{ bg: "blue.500", color: "white", boxShadow: "md", transform: "scale(1.08)" }}
                  _hover={{ bg: "blue.100", color: "blue.700" }}
                  display="flex"
                  alignItems="center"
                  gap={3}
                >
                  <FaShoppingCart />
                  Basquete
                </Tabs.Trigger>
                <Tabs.Trigger
                  value="settings"
                  fontSize="xl"
                  fontWeight="bold"
                  px={12}
                  py={5}
                  borderRadius="lg"
                  transition="all 0.2s"
                  _selected={{ bg: "blue.500", color: "white", boxShadow: "md", transform: "scale(1.08)" }}
                  _hover={{ bg: "blue.100", color: "blue.700" }}
                  display="flex"
                  alignItems="center"
                  gap={3}
                >
                  <LuSquareCheck />
                  Corrida
                </Tabs.Trigger>
              </Tabs.List>
              <Tabs.Content value="loja" bg="white" borderRadius="md" mt={4} boxShadow="sm" p={6}>
                <InputPesquisa searchTerm={searchTerm} SetSeachTerm={setSearchTerm} />
                {/* Conteúdo da Loja */}
              </Tabs.Content>
              <Tabs.Content value="carrinho" bg="gray.100" borderRadius="md" mt={4} boxShadow="sm" p={6}>
                {/* Conteúdo do Carrinho */}
              </Tabs.Content>
              <Tabs.Content value="settings" bg="blue.50" borderRadius="md" mt={4} boxShadow="sm" p={6}>
                {/* Conteúdo de Settings */}
              </Tabs.Content>
            </Tabs.Root>
          </>
        )}
        {sidebarSection === "carrinho" && (
          <Box mt={10} color="white" fontSize="2xl">
            {/* Conteúdo do Carrinho principal */}
            Carrinho principal selecionado!
          </Box>
        )}
        {sidebarSection === "config" && (
          <Box mt={10} color="white" fontSize="2xl">
            {/* Conteúdo de Configurações */}
            Configurações selecionado!
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default tabInicial;